* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  color: #2c2c2c;
  background-color: #afafaf;
}

.title {
  text-align: center;
}

.card {
  background-color: #ebebeb;
  border-radius: 10px;
  padding: 25px;
}

.center-xy {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.center-y {
  position: absolute;
  top: 50%;
}

.center-x {
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.fullScreen {
  width: 100vw;
  height: 100vh;
}

